@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-family: "Noto Sans JP", sans-serif;
}

body {
  background-image: radial-gradient(white, black);
}

.App {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
}

.Square_square__1JHmp {
  flex: 33% 1;
  height: 100%;
  border: 1px solid rgb(0, 0, 0);
  cursor: pointer;
  display: grid;
  place-items: center;
  font-size: 50px;
  color: rgb(0, 0, 0);
}

.Table_board__gvu3M {
  width: 700px;
  height: 700px;
  background-color: rgb(82, 83, 61);
  border-color: 1px rgb(0, 0, 0);
  display: flex;
  flex-direction: column;
}
.Table_row__3w88S {
  flex: 33% 1;
  display: flex;
  flex-direction: row;
}
.Table_current__CnIXr {
  font-size: 20px;
  color: bisque;
  margin-bottom: 10px;
}
.Table_max__1cQGv {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 800px;
  padding: 20px;
}

.Button_button__3HdYY {
  background-color: rgb(0, 0, 0);
